<template>
  <div class="order">
    <header>
      <i class="iconfont icon-fanhui" @click="backNextPage"></i>
      <span>提交订单</span>
      <i class="iconfont icon-xiaoxi"></i>
    </header>
    <section ref="wrapper">
      <div>
        <span class="shxx">收货信息:</span>
        <div class="userDZ">
          <div class="userDZ_l">
            <ul>
              <li>
                <span>{{ userDefaults?.name }}</span>
                <span>{{ userDefaults?.tel }}</span>
              </li>
              <li>
                <span style="font-size: 0.4rem">{{
                  userDefaults?.cityCounty
                }}</span
                ><span style="font-size: 0.4rem">{{
                  userDefaults?.addressDetail
                }}</span>
              </li>
            </ul>
          </div>
          <div class="userDZ_r">
            <i class="iconfont icon-liebiao" @click="changeTackDelivery"></i>
          </div>
        </div>
        <div class="zffs">
          <ul>
            <li><span>支付方式：</span><b>选择在线支付,随机减金额</b></li>
            <li class="gezhozhifu">
              <span class="gezhozhifuBorder">支付宝支付</span
              ><span>微信支付</span><span>货到付款</span>
              <span>信用卡支付</span>
            </li>
          </ul>
        </div>
        <div class="spxx" v-for="(item, index) in shopList" :key="index">
          <div class="spxq">
            <div class="spimg">
              <img
                :src="item.goods_imgUrl"
                style="width: 2.5rem; height: 2.5rem"
              />
            </div>

            <div class="spqtxx">
              <ul>
                <li>{{ item.goods_name }}</li>
                <li><span>商品规格:无</span></li>
                <li class="spanli">
                  <b>￥{{ item.goods_price }}</b>
                  <span>{{ item.goods_num }}</span>
                </li>
              </ul>
            </div>
          </div>
          <div class="maijialiuyan">
            <input type="text" placeholder="给卖家留言" />
          </div>
        </div>
        <span class="shxx">账单明细:</span>

        <div class="ddzj">
          <ul>
            <li>
              <span>订单总价:</span><b>￥{{ sumPrice }}</b>
            </li>
            <li><span>运费:</span><b>￥10.00</b></li>
            <li><span>优惠卷:</span><b>-￥0.00</b></li>
            <li>
              <span>实际付款:</span><b>￥{{ sumPrice + 10 }}</b>
            </li>
          </ul>
        </div>
        <van-notice-bar
          left-icon="volume-o"
          text="订单在12小时内未支付将自动取消"
        />
      </div>
    </section>
    <footer>
      <div class="f-left">
        <span
          >共<b>{{ sumNum }}</b
          >件，总金额<b>￥{{ sumPrice + 10 }}</b>
        </span>
      </div>
      <div class="f-right" @click="submitOrder">提交订单</div>
    </footer>
  </div>
</template>

<script>
import Vue from "vue";
import BScroll from "better-scroll"; //页面滚动插件
import { NoticeBar } from "vant";
import { userAddList } from "@/api/useraddress.js";
import { mapState } from "vuex";
import { changeOrderMsg, payOrder } from "@/api/order.js";
import { deleteAllShopList } from "@/api/shopCart.js";
Vue.use(NoticeBar);
export default {
  name: "order-vue",
  data() {
    return {
      addressList: [], //存储用户所有的地址信息
      shopList: [], //存储用户选中商品的信息
      thisaddList: [], //存储用户选取的地址信息
      sumNum: 0,
      sumPrice: 0,
    };
  },
  created() {
    let arr = this.$route.params.selectList;
    let sum = 0;
    let prcie = 0;
    for (let i of this.list) {
      if (arr.indexOf(i.id) != -1) {
        this.shopList.push(i);
        sum = sum + parseInt(i.goods_num);
        prcie = prcie + i.goods_num * i.goods_price;
      }
    }
    this.sumNum = sum;
    this.sumPrice = prcie;
  },
  activated() {
    if (this.$route.params.item) {
      this.thisaddList.push(JSON.parse(this.$route.params.item));
    }
  },
  methods: {
    backNextPage() {
      this.$router.push("/cart");
    },
    async getUserAddress() {
      let res = await userAddList();
      if (res.data.code == 200) {
        this.addressList = res.data.data.r;
      }
    },
    //用户点击改变收获地址的回调函数
    changeTackDelivery() {
      this.$router.push({
        name: "AddressIndex",
        params: {
          path: true,
        },
      });
    },
    async submitOrder() {
      let idArr = [];
      for (let i of this.shopList) {
        idArr.push(i.id);
      }
      let res = await changeOrderMsg(this.productToken);
      if (res.data.code == 200) {
        let r = await deleteAllShopList(idArr);
        if (r.data.code == 200) {
          //页面跳转
          let rr = await payOrder(this.productToken);
          if (rr.data.code == 200) {
            //打开支付页面
            window.location.href = rr.data.data.payMentUrl;
          }
        }
      }
    },
  },
  mounted() {
    this.getUserAddress();

    this.$nextTick(() => {
      new BScroll(this.$refs.wrapper, {
        movable: true,
        zoom: true,
        click: true,
      });
    });
  },
  computed: {
    //用户默认地址
    userDefaults() {
      if (this.thisaddList.length == 0) {
        let newArr = {};
        this.addressList.forEach((item) => {
          if (item.isDefault == 1) {
            newArr = item;
          }
        });
        if (newArr.id) {
          return newArr;
        } else {
          return this.addressList[0];
        }
      } else {
        return this.thisaddList[this.thisaddList.length - 1];
      }
    },
    ...mapState({
      list: (state) => state.cart.list,
    }),
    ...mapState({
      productToken: (state) => state.cart.productToken,
    }),
  },
};
</script>

