    display: flex;
    width: 100%;
    height: 1.74rem;
    background-color: #b0352f;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    padding: 0 0.5rem;
    i {
      font-size: 0.7rem;
    }
    span {
      font-size: 0.5rem;
    }
  }
  section {
    box-sizing: border-box;
    padding: 0.2rem 0;
    flex: 1;
