      width: 100%;
      height: 2rem;
      background-color: #fff;
      padding-left: 0.4rem;
      padding-right: 0.3rem;
      margin-top: 0.3rem;
      .userDZ_l {
        width: 85%;
        height: 100%;
        ul {
          width: 100%;
          height: 100%;
          li {
            width: 100%;
            height: 50%;
            line-height: 1rem;
            font-size: 0.5rem;
            font-weight: 500;
            b {
              font-weight: 500;
              color: red;
              margin-right: 0.3rem;
            }
            span {
              margin-right: 0.2rem;
            }
          }
        }
      }
      .userDZ_r {
        height: 100%;
        width: 10%;
        i {
          font-size: 0.7rem;
          font-weight: 500;
          line-height: 2rem;
        }
      }
    }
    .zffs {
      padding: 0 0.4rem;
      margin-top: 0.5rem;
      width: 100%;
      height: 2rem;
      background-color: #fff;
      font-size: 0.4rem;
      ul {
        li {
          height: 1rem;
          line-height: 1rem;
          b {
            padding: 0 0.3rem;
